.header{
  max-width: 100%;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
  /* background-color: #3399ff; */
  background: -webkit-gradient(linear, left top, right top, from(#0066cc), color-stop(#0080ff), color-stop(#3399ff), color-stop(#3399ff), color-stop(#0080ff), to(#0066cc));
  background: -webkit-linear-gradient(left, #0066cc, #0080ff, #3399ff, #3399ff, #0080ff, #0066cc);
  background: -o-linear-gradient(left, #0066cc, #0080ff, #3399ff, #3399ff, #0080ff, #0066cc);
  background: linear-gradient(to right, #0066cc, #0080ff, #3399ff, #3399ff, #0080ff, #0066cc);
  /* margin-bottom: 50px; */
}



